.btn__styles {
  box-sizing: border-box;
  display: flex;
  padding: 16px 32px;
  gap: 16px;
  font-weight: 700;
  font-size: 23px;
  line-height: 27px;
  height: 59px;
  border: 2px solid #005259;
  border-radius: 5px;
  display: flex;
  align-items: center;
  cursor: pointer;
  white-space: nowrap;
}

@media screen and (max-width: 700px) {
  .btn{
    justify-content: center;
  }
}